import {loadingMutation} from './mutations/loading.js'
import {$ajax} from './mutations/ajax.js'
import {api} from './mutations/api.js'
const mutationsobj = {
	...loadingMutation,
	doctitleFn (state,title) {
		state.doctitle = title
	},
	$ajax (state, axios, store) {
		state.axios = axios.axios
	},
	...$ajax,
	...api,
	setvue (state, vm) {
		state.vm = vm
	},
	qs (state, obj) {
		state.qs = obj.qs
	},
	alert (state,obj) {
	  var vm = obj.vm
      state.alertsu = function (text,title,time) {
      	vm.$notify({
          title: title || '成功提示',
          message: text,
          type: 'success',
          duration:  time || 10000
        });
      }
      state.alerter = function (text,title,time) {
      	vm.$notify.error({
	        title: title || '失败提示',
	        message: text,
	        duration: time || 10000
	    });
      }
      state.confirm = function (obj) {
      	vm.$confirm(obj.content, obj.title || '提示', {
          confirmButtonText: '确定',
          cancelButtonText:  '取消',
          type: obj.type || 'warning'
        }).then((action) => {
          obj.confirmCallback && obj.confirmCallback(action)
        }).catch((action) => {
          obj.cancelCallback && obj.cancelCallback(action)         
        });
      }
      state.alert = function (obj) {
      	vm.$alert(obj.content, obj.title, {
          confirmButtonText: '确定',
          callback: action => {
            obj.confirmCallback && obj.confirmCallback(action)
          }
        });
      }
	},
  addTabs (state,obj) {
    state.tabs.push(obj)
  },
	setstore (state, obj) {
		state.store = obj.store
	},
  windowresize (state) {
    var setGlobal = function () {
      var w = document.documentElement.clientWidth
      if (w <= 769) {
        state.menuLayout = true
        state.panelLayout = 'top'
        state.modalWidth = '90%'
      } else {
        state.menuLayout = false
        state.panelLayout = 'right'
        state.modalWidth = '60%'
      }
    }
    setGlobal();
    window.onresize = function () {
      setGlobal();
      state.setMainHeight && state.setMainHeight()
    }
  }
}
export var mutations = mutationsobj


