export var $ajax = {
	async $get (state,obj) {
		let objj = obj.obj
		let paramm = obj.param
		let vm = state.vm.vm
		var params = {
			url: ''
		}
		paramm.urlJurisdiction = window.location.hash.replace('#','').split('?')[0]
		state.loading = true
		params = Object.assign(params, objj)
		await state.axios.get(params.url, {
			params: paramm,
			withCredentials: true
		}).then(function (response) {
			if (response.data.code == 10000 || response.data.code == 20000 || response.data.code == 30000) {
				window.location.href = state.systemPage + "/loginpage"
				return false
			}
			objj.success(response)
		}).catch(function (error) {
			objj.error && objj.error(error)
		});
		setTimeout(() => state.loading = false, 200)
		
	},
	async $post (state,obj) {
		console.log(obj)
		let vm = state.vm.vm
		state.loading = true
		// obj.data.params.urlJurisdiction = window.location.hash.replace('#','')
		await state.axios.post(obj.url, obj.data.formData_, {
			headers: obj.data.headers,
			responseType: "json",
    	  	withCredentials: true
		}).then(function (response) {
			if (response.data.code == 10000 || response.data.code == 20000 || response.data.code == 30000) {
				window.location.href = state.systemPage + "/loginpage"
				return false
			}
			obj.success(response)
		}).catch(function (error) {
			console.log(error)
			obj.error && obj.error(error)
			vm.$notify.error({
	          title: '请求出错',
	          message: '',
	          duration: 10000
	        });
		});
		setTimeout(() => state.loading = false, 200)
	},
	async $axios (state,objs) {
		let obj = objs.obj
		let vm = state.vm.vm
		obj.data.urlJurisdiction = window.location.hash.replace('#','').split('?')[0]
		const options = {
		  method: obj.method,
		  headers: obj.header,
		  data: JSON.stringify(obj.data),
		  // params: obj.data,
		  url: obj.url,
		  xsrfCookieName: 'csrftoken',  // default: XSRF-TOKEN
    	  xsrfHeaderName: 'x-csrftoken',   // default: X-XSRF-TOKEN
    	  responseType: "json",
    	  withCredentials: true
		};
		state.loading = true
		await state.axios(options).then(function(res){
			if (res.code == 10000 || res.code == 20000 || res.code == 30000) {
				window.location.href = state.systemPage + "/loginpage"
				return false
			}
			obj.success(res)
		}).catch(function(err){
			vm.$notify.error({
	          title: '请求出错',
	          message: '',
	          duration: 10000
	        });
		})
		state.loading = false
	}
}


