export default {
	emotionr: {
		namespaced: true,
		state: {
			title: '情感管理2'
		},
		mutations: {
			ala() {
				console.log('情感管理2')
			}
		},
		modules: {
			emotionp: {
				namespaced: true,
				state: {
					name: 'txp**'
				},
				mutations: {
					alp() {
						console.log('88888')
					}
				},
				modules: {
					emotiona: {
						namespaced: true,
						mutations: {
							al() {
								console.log('情感')
							}
						}
					}
				}
			}
		}
		
	},
	emotionr2: {
		namespaced: true,
		state: {
			title: '2222222222'
		},
		mutations: {
			alaqq() {
				console.log('2222222222')
			}
		},
		modules: {
			emotionr22: {
				namespaced: true,
				mutations: {
					alpvv() {
						console.log('alpvvalpvvalpvvalpvvalpvv')
					}
				}
			}
		}
	},
	createTree: {
		namespaced: true,
		state: {
			tree1 (menudata,obj) {
				var _this = this
				var dataArray = [];
				var datas = menudata
				datas.forEach(function(item) {
				  var cid = item.cid;
				  if(cid == 0) {
				      item.label = item.menu_name
				      item.title = item.menu_name
				      for(let key in obj) {
				      	item[key] = item[obj[key]]
				      }
				      dataArray.push(item);
				  }
				})
				return [datas, dataArray]
			},
			data2treeDG (datas, dataArray,obj) {
				var _this = this
				for(var j = 0; j < dataArray.length; j++) {
				    var dataArrayIndex = dataArray[j];
				    var childrenArray = [];
				    var id = dataArrayIndex.id;

				    for(var i = 0; i < datas.length; i++) {
				        var data = datas[i];
				        var cid = data.cid;
				        if(cid == id) {//判断是否为儿子节点
				            data.label = data.menu_name
				            data.title = data.menu_name
							for(let key in obj) {
						      	data[key] = data[obj[key]]
						    }
				            childrenArray.push(data);
				        }

				    }
				    dataArrayIndex.children = childrenArray;
				    if(childrenArray.length > 0) {//有儿子节点则递归
				        _this.data2treeDG(datas, childrenArray,obj);
				    }

				}
				return dataArray;
			}
		}
	}
}


