import { stateData } from './state/state.js'
import {catchobj} from './mutations/catch.js'
let loca = loca = window.location
let locationConfig = {
	protocol: loca.protocol,
	host: loca.host,
	hash: loca.hash,
	hostname: loca.hostname,
	href: loca.href,
	origin: loca.origin,
	pathname: loca.pathname,
	port: loca.port

}

const state = {
	...stateData,
	...catchobj,
	api: null,
	axios: null,
	doctitle: [],
	vm: null,
	ApiHost: locationConfig.hostname == '127.0.0.1' ? 'http://127.0.0.1' : 'https://www.xiaoweiba8.com',
	staticPath: locationConfig.hostname == '127.0.0.1' ? 'http://127.0.0.1/public' : 'https://www.xiaoweiba8.com/public',
	systemPage: locationConfig.hostname == '127.0.0.1' ? 'http://127.0.0.1' : 'https://www.xiaoweiba8.com',
	qs: null,
	alertsu: null,
	alerter: null,
	store: null,
	confirm: null,
	alert: null,
	userName: '',
	menudata: null,
	tabs: [],
	menuLayout: false,
	panelLayout: 'left',
	modalWidth: '60%',
	userInfo: null,
	themeEditDataCache: null,
	setMainHeight: null
}
export default state


