import Vue from 'vue'
import Router from 'vue-router'
const login = r => require.ensure([], () => r(require('@/components/login/login')), 'login')
Vue.use(Router)
const routeArr = [
  {
      path: '/',
      name: 'login',
      component: login,
      meta: {
        title: [
          {
            name: '主面板',
            path: '/'
          }
        ]
      }
  }
]
export default new Router({
  routes: routeArr
})



