export var api = {
	api (state,obj) {
		var apiConfig = {
			createClassAMenu: state.ApiHost + '/ccam',
			index: state.ApiHost + '/',
			getmenu: state.ApiHost + '/getmenu',
			getusermenu: state.ApiHost + '/getusermenu',
			deletemenu: state.ApiHost + '/deletemenu',
			createuser: state.ApiHost + '/createuser',
			adminlogin: state.ApiHost + '/login',
			getuser: state.ApiHost + '/getuser',
			logout: state.ApiHost + '/logout',
			uploadUserMenu: state.ApiHost + '/uploadUserMenu',
			deleteuser: state.ApiHost + '/deleteuser',
			// 添加主题
			addtheme: state.ApiHost + '/addtheme',
			// 主题缩略图上传
			uploadUrl: state.ApiHost + '/uploadfile',
			// 获取主题数据
			gettheme: state.ApiHost + '/gettheme',
			closetheme: state.ApiHost + '/closetheme',
			modifyhhemenumber: state.ApiHost + '/modifyhhemenumber',
			// 情感管理缩略图上传
			emotioinminimg: state.ApiHost + '/emotioinminimg',
			// 添加情感数据文本部分
			addemotiontext: state.ApiHost + '/addemotiontext',
			// 幻灯片上传
			slideUploadUrl: state.ApiHost + '/slideuploadurl',
			// 课程缩略图
			emotionUploadMinImg: state.ApiHost + '/emotionuploadminimg',
			// 图库上传
			libsimg: state.ApiHost + '/libsimg',
			getlibsimg: state.ApiHost + '/getlibsimg',
			// 禁用图片库里的图片
			deleteImgLib: state.ApiHost + '/deleteimglib',
			// 更新图文编辑
			uploadImgText: state.ApiHost + '/uploadimgtext',
			// 音频上传
			radioUpload: state.ApiHost + '/radioppload',
			// 视频上传
			videoUpload: state.ApiHost + '/videoupload',
			// 获取课时数据
			getEmotionData: state.ApiHost + '/getemotiondata',
			deletevideo_upload_jurisdiction: state.ApiHost + '/deletevideo_upload_jurisdiction',
			emotionToExamine: state.ApiHost + '/emotiontoexamine'
		}
		state.api = apiConfig
	}
}


