const beforeEachFn = {
	showLoading (store) {
		store.commit('showLoading')
	},
	hideLoading (store) {
		store.commit('hideLoading')
	},
	setDocTitle (store, title) {
		store.commit('doctitleFn',title)
		// store.commit('emotionr/ala')
		// store.commit('emotionr/emotionp/emotiona/al')
		// store.commit('emotionr/emotionp/alp')

		// store.commit('emotionr2/alaqq')
		// store.commit('emotionr2/emotionr22/alpvv')
	}
}
export default beforeEachFn


